import React from "react";

import { graphql, useStaticQuery } from "gatsby";

import { IProjectPreviewQueryResult } from "../../types/project.type";

import ProjectPreview from "./projectPreview";
import TemplateSection from "../common/templateSection";

const ProjectsSection = ({
  title,
  excludeId,
}: {
  title?: string;
  excludeId?: string;
}) => {
  const projectsData = useStaticQuery(query);

  let projects = (
    projectsData?.allMarkdownRemark?.nodes?.length
      ? projectsData.allMarkdownRemark.nodes
      : null
  ) as IProjectPreviewQueryResult[] | null;

  if (!projects) return <></>;

  if (excludeId) {
    projects = projects.filter((project) => project.id !== excludeId);
  }

  return (
    <TemplateSection
      section="projects"
      title={title ? title : "OUR PROJECTS"}
      className="bg-main-100 text-secondary-100"
    >
      <div className="flex justify-between flex-col items-center sm:items-stretch sm:flex-row">
        {projects.map((project, i) => {
          if (i === 3) return;
          return (
            <ProjectPreview
              key={project.id}
              project={project}
              theme="light"
              className=" basis-[45%] max-w-[80%] last:hidden md:last:flex md:basis-[30%] mt-16 sm:mt-0"
            />
          );
        })}
      </div>
    </TemplateSection>
  );
};

export default ProjectsSection;

export const query = graphql`
  query {
    allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "projects" } } }
      sort: { frontmatter: { date: DESC } }
      limit: 4
    ) {
      nodes {
        id
        frontmatter {
          title
          cover {
            childImageSharp {
              gatsbyImageData(
                width: 600
                formats: [AUTO, WEBP, AVIF]
                placeholder: BLURRED
              )
            }
          }
        }
      }
    }
  }
`;
